.page-container {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: space-between;
    align-items: center;
}

.msgWriter__placeholder {
    display: flex;
    align-content: center;
    align-items: center;
    height: 10rem;
}

.answerBtn {
  align-items: center;
  background: #FFFFFF;
  border: 0 solid #E2E8F0;
  box-shadow: 0 4px 6px -1px #00a8e8ff, 0 2px 4px -1px #007ea7ff;
  box-sizing: border-box;
  display: inline-flex;
  font-family: Inter, sans-serif;
  font-size: 1rem;
  font-weight: 700;
  height: 2rem;
  justify-content: center;
  line-height: 24px;
  overflow-wrap: break-word;
  padding: 24px;
  text-decoration: none;
  width: 25rem;
  border-radius: 8px;
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.answerBtn:disabled {
  color: gray;
  box-shadow: 0 4px 6px -1px gray, 0 2px 4px -1px #00171fff;
}

.correctAnswer:disabled {
  color: #499F68;
  box-shadow: 0 4px 6px -1px #499F68, 0 2px 4px -1px #4CAF50;
}

.incorrectAnswer:disabled {
  color: #BA274A;
  box-shadow: 0 4px 6px -1px #BA274A, 0 2px 4px -1px #f44336;
}

.answer__container {
    display: flex;
    width: 55rem;
    justify-content: space-between;
    margin-top: 2rem;
    flex-direction: row;
    margin-top: 2rem;
  }

.answer__inner-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 8rem;
}

.question__text {
    font-size:x-large;
    text-align: center;
    line-height: 1.7;
    justify-self: center;
    align-self: center;
    font-weight: 500;
}

.question__div {
    height: 12rem;
    display: flex;
    justify-content: center;
    justify-items: center;
    margin-bottom: 1rem;
}
.nextBtn__container {
    display: flex;
    flex-direction: row-reverse;
    width: 55rem;
}

.nextBtn {
    align-self: flex-end;
}

.fa-fish {
    color: #007ea7;
    position: relative;
}

i:after {
    content: 'Next';
    position: absolute;
    top: 50%;
    left: 45%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: medium;
    font-weight: 800;
    color: #FFF;
}

.nextBtn__text {
    font-family: inherit;
}
h1 {
    font-size: 3.5rem;
}
.typewriter__container {
    align-self: center;
    color: #e0acd5ff;
}

.typewriter__text {
    color: #e0acd5ff;
}
.logo__container {
    position: relative;
}

.logo__img {
    position: absolute;
    top: 8px;
    left: 10rem;
    font-size: 18px;
}
.fish__container {
    position: absolute;
}
footer {
  position: fixed;
  background: linear-gradient(to bottom, #ffffff 0%, #7bdff2 100%);
  bottom: 0;
  left: 0;
  right: 0;
  padding: 6px 10px 6px 10px;
  text-align: center;
}

